.nav {
  text-transform: uppercase;
  font-size: 14px; }
  .nav ul {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex; }
  .nav li {
    padding: 0 11px 0 18px; }
  .nav a {
    color: #fff;
    display: block;
    position: relative; }
    .nav a:after {
      display: none;
      position: absolute;
      content: "";
      left: 0;
      top: 100%;
      width: 100%;
      height: 30px;
      background: transparent; }
  .nav .drop-down {
